@charset "UTF-8";

.box {
    position: relative;
    flex: 1 0 230px;
    width: 230px;
    vertical-align: middle;
    transition: all 0.5s ease-in-out;
    -webkit-transition: all 0.5s ease-in-out;
}

.box .imgWrap {
    position: relative;
    display: block;
    overflow: hidden;
}

.box .imgWrap:after {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    content: "";
    transform: scale(0.9);
    transition: all 0.5s ease-in-out;
    -webkit-transform: scale(0.9);
    -webkit-transition: all 0.5s ease-in-out;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
    opacity: 0;
}

.box .imgWrap img {
    position: relative;
    display: inline-block;
    width: 100%;
    transform: scale(0.9);
    transition: all 0.5s ease-in-out;
    -webkit-transform: scale(0.9);
    -webkit-transition: all 0.5s ease-in-out;
}

.box .imgWrap .info {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 60px 20px;
    transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    opacity: 0;
    z-index: 200;
}
.box .imgWrap .info .top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.box .imgWrap .info .left {
    font-size: 1.6rem;
    color: var(--themeColor);
}

.box .imgWrap .info .left span {
    display: inline-block;
    margin-left: 5px;
}

.box .imgWrap .info .right {
    display: inline-block;
    align-self: flex-end;
    padding: 2px 6px;
    font-size: 0.9rem;
    color: var(--grey);
    border: 1px solid var(--grey);
    border-radius: 5px;
}

.box .imgWrap .info ul {
    margin-top: 20px;
}

.box .imgWrap .info ul li {
    color: var(--grey);
}

.box h3 {
    margin-top: 4px;
    padding: 0 12px;
    color: var(--lightGrey);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-family: "Encode Sans", sans-serif;
    font-weight: 300;
}

.box:hover .imgWrap:after {
    -webkit-transform: scale(1);
    transform: scale(1);
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 100;
    opacity: 1;
}

.box:hover .imgWrap img {
    -webkit-transform: scale(1);
    transform: scale(1);
}

.box:hover .imgWrap .info {
    opacity: 1;
}

/* Mobile */
@media screen and (max-width: 768px) {
    .box {
        margin: 70px auto;
    }

    .box:first-child {
        margin-top: 10px;
    }

    .box:hover .imgWrap:after {
        opacity: 0;
    }

    .box .imgWrap img {
        transform: scale(1);
        -webkit-transform: scale(1);
    }

    .box .imgWrap .info {
        position: relative;
        padding: 0;
        opacity: 1;
    }

    .box .imgWrap .info ul {
        margin-top: 8px;
    }

    .box h3 {
        display: none;
    }
}
