@charset "UTF-8";

.content {
    position: relative;
    margin-top: -26px;
    height: 500px;
    overflow: hidden;
}

.info {
    position: relative;
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-start;
    gap: 4rem;
    padding: 4rem 4rem 4rem 0;
    width: 100%;
    height: 100%;
    z-index: 1;
}

.info::after {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    content: "";
    background: linear-gradient(
        to right,
        rgba(18, 18, 18, 1),
        rgba(18, 18, 18, 0.5)
    );
    z-index: -1;
}

.info h2 {
    margin-bottom: 12px;
    color: var(--lightGrey);
    font-size: 2rem;
    font-family: "Pathway Gothic One", sans-serif;
}

.info .img {
    width: 230px;
    height: auto;
}

.info .rating {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    margin-bottom: 10px;
    color: var(--themeColor);
    font-size: 1.3rem !important;
}

.info .rating svg {
    margin-right: 2px;
}

.info .year {
    margin-left: 14px;
    font-size: 1.1rem;
    color: var(--lightGrey);
}

.info ul {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    gap: 12px;
}

.info ul li {
    padding: 2px 8px;
    font-size: 0.8rem;
    color: var(--grey);
    border: 1px solid var(--grey);
    border-radius: 6px;
}

.info .text {
    margin-top: 1rem;
    font-size: 1.1rem;
    color: var(--grey);
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 10;
    overflow: hidden;
}

/* bg image */
.bgWrap {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: auto;
}

.bgWrap .bgInner {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: -1;
}

.bgWrap .bgInner:after {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    content: "";
    background-color: rgba(18, 18, 18, 0.6);
}

.bgWrap img {
    width: 100%;
    height: auto;
}

@media screen and (max-width: 1200px) {
    .content {
        margin-top: 0;
        height: auto;
    }

    .info {
        padding: 3rem 3rem 3rem 0;
        gap: 3rem;
    }

    .info .text {
        -webkit-line-clamp: 6;
    }
}

/* Mobile */
@media screen and (max-width: 768px) {
    .info {
        display: block;
        padding: 2.5rem;
        text-align: center;
    }

    .info::after {
        background: linear-gradient(
            145deg,
            rgba(18, 18, 18, 1),
            rgba(18, 18, 18, 0.6)
        );
    }

    .info .img {
        margin-bottom: 2.5rem;
        width: 250px;
    }

    .info .img + div {
        text-align: initial;
    }

    .info h2 {
        font-size: 1.8rem;
    }

    .bgWrap img {
        width: auto;
        height: 100%;
    }
}
