@import url("https://fonts.googleapis.com/css2?family=Pathway+Gothic+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Encode+Sans:wght@300;400;500;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Material+Symbols+Rounded:opsz,wght,FILL,GRAD@20..48,100..700,0..1,-50..200");

/* reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
fieldset,
img {
    border: 0 none;
}
dl,
ul,
ol,
menu,
li {
    list-style: none;
}
blockquote,
q {
    quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}
input,
select,
textarea,
button {
    font-size: 100%;
    vertical-align: middle;
}
button {
    border: 0 none;
    background-color: transparent;
    cursor: pointer;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
body {
    -webkit-text-size-adjust: none;
} /* 뷰표트 변환시 폰트크기 자동확대 방지 */
input[type="text"],
input[type="password"],
input[type="submit"],
input[type="search"] {
    -webkit-appearance: none;
    border-radius: 0;
}
input:checked[type="checkbox"] {
    background-color: #666;
    -webkit-appearance: checkbox;
}
button,
input[type="button"],
input[type="submit"],
input[type="reset"],
input[type="file"] {
    -webkit-appearance: button;
    border-radius: 0;
}
input[type="search"]::-webkit-search-cancel-button {
    -webkit-appearance: none;
}
a {
    text-decoration: none;
}
a:active,
a:hover {
    text-decoration: underline;
}
address,
caption,
cite,
code,
dfn,
em,
var {
    font-style: normal;
    font-weight: normal;
}
img {
    vertical-align: top;
    max-width: 100%;
}

/* Variables */
:root {
    --webWidthL: 1200px;
    --webWidthS: 94%;
    --tabletWidth: 94%;
    --mobileWidth: 96%;

    --darkTheme: rgb(18, 18, 18);
    --themeColor: #fdcc0d;
    --white: white;
    --black: black;
    --lightGrey: #e1e5ea;
    --grey: rgb(180, 180, 180);
    --darkGrey: #5d5d5d;
}

/* Common */

body {
    background-color: var(--darkTheme);
}

#root > div {
    width: 100%;
    min-width: 320px;
}

header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    min-width: 320px;
    background-color: var(--darkTheme);
    z-index: 1000;
}
header > div {
    margin: 0 auto;
    padding: 20px 0;
    width: var(--webWidthL);
}
header a {
    display: inline-block;
}

header a h1 {
    font-size: 1.8rem;
    color: var(--lightGrey);
    font-family: "Pathway Gothic One", sans-serif;
}

header a:hover h1 {
    color: var(--themeColor);
}

.container {
    width: 100%;
}
.container > .inner {
    margin: 0 auto;
    padding: 100px 0 70px;
    width: var(--webWidthL);
}

/* Home.js */
.boxWrap {
    display: flex;
    flex-flow: row wrap;
    row-gap: 5rem;
    justify-content: space-between;
    margin: 0 -12px;
}

.loading {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    color: var(--grey);
}

@media screen and (max-width: 1200px) {
    header > div {
        width: var(--webWidthS);
    }

    .container > .inner {
        padding-top: 72px;
        width: var(--webWidthS);
    }
}

/* Tablet */
@media screen and (max-width: 1024px) {
    header > div {
        text-align: center;
    }

    .container > .inner {
        width: var(--tabletWidth);
    }
}

/* Mobile */
@media screen and (max-width: 768px) {
    header > div {
        padding: 16px;
    }

    header a h1 {
        font-size: 1.6rem;
    }

    .container > .inner {
        width: var(--mobileWidth);
        padding: 62px 0 0;
    }

    .boxWrap {
        display: block;
        margin: 0;
    }
}
